import React, { FC, useCallback, useState, useMemo } from 'react';
import { useHistory } from 'umi';
import { Pagination, Spin, Button, message, Empty, Space } from 'antd';
import { Role } from '@/utils/authority';
import { withAuthorized } from '@/utils/Authorized';
import CommonItemWrap from '@/components/CommonItemWrap';
import style from './index.less';
import AddExpertModal from '@/components/AddExpertModal';
import UserInfoHead from '@/components/UserInfHead';
import { UserOutline, UserBlack } from '@/components/miniComponents';

import { updateExpertUser } from '../ExpertList/services';
import { useGetUserDetail, useGetProductionGroupDetail } from '@/services/publicServices';
import { useGetUserCommontList } from './services';
import { CommontItem } from '@/types/types';

const PAGESIZE = 10;

const UserCommontList = (props: any) => {
  const { userId, isExport } = useMemo(() => {
    return {
      userId: props?.match?.params?.id,
      isExport: Boolean(props?.location?.query?.isExport)
    };
  }, [props]);

  const initSearch = {
    pagination: { pageSize: PAGESIZE, current: 1 },
    query: {
      commentUserId: userId,
    }
  };

  const [addExpertVisible, setAddExpertVisible] = useState(false);
  const [searchParams, setSearchParams] = useState(initSearch);

  const { userInfo, userInfoLoading, refreshUser } = useGetUserDetail(userId);
  const { commontList, pagination, CommontCount, isValidating, mutate } = useGetUserCommontList(searchParams);

  const productionIds = useMemo(() => {
    return commontList?.map((item: CommontItem) => item?.subjectId) || [];
  }, [commontList]);
  const { productionGroupDetail, detailLoading } = useGetProductionGroupDetail(productionIds);

  const handleShowAddExpertVisible = useCallback(() => {
    setAddExpertVisible(true);
  }, []);
  const handleHiddenAddExpertVisible = useCallback(() => {
    setAddExpertVisible(false);
  }, []);
  const history = useHistory();

  const handleAddExpert = useCallback(async (params: any) => {
    try {
      const res = await updateExpertUser(params);

      if (res?.code === 200) {
        message.success('操作成功！');
        handleHiddenAddExpertVisible();
        refreshUser();
      }
    } catch (error) {
      console.error(error?.message || '服务器出问题啦');
    }

  }, [handleHiddenAddExpertVisible, refreshUser]);

  const handleChangePage = useCallback((current: number) => {
    setSearchParams((preParams: any) => ({
      ...preParams,
      pagination: { pageSize: PAGESIZE, current },
    }));
  }, []);

  const handleAddExpertCommomt = useCallback(() => {
    sessionStorage.setItem('expertInfo', JSON.stringify(userInfo));
    history.push('/commont-manage/expert-comment-manage');
  }, [userInfo]);

  return (
    <div className={ style.userCommontListWrap }>
      <AddExpertModal targetExport={ userInfo } visible={ addExpertVisible } onCancel={ handleHiddenAddExpertVisible } onOk={ handleAddExpert } />
      <Spin tip="加载中..." spinning={ userInfoLoading }>
        <UserInfoHead user={ userInfo } isExport={ isExport }
          btnRender={ ( isExport
            ? (
              <>
                {/* <UserOutline user={ userInfo } isExpert={ true } refresh={ refreshUser } /> */}
                <Button type="primary" style={{ marginLeft: '10px' }} onClick={ handleShowAddExpertVisible }>修改资料</Button>
              </>)
            : (
              <Space size={ 8 }>
                <UserBlack user={ userInfo } isEnter={ true } />
                {/* // refresh={ mutate }  */}
                {/* <UserOutline user={ userInfo } refresh={ refreshUser } /> */}
              </Space>
            ) ) }
        />
      </Spin>
      <Spin tip="加载中..." spinning={ isValidating }>
        <div className={ style.userCommontContent }>
          <div className={ style.userCommontChunkHead }>
            <span>{isExport ? '我的专业评论' : '评论列表'}</span>
            {isExport ? <Button type="primary" onClick={ handleAddExpertCommomt }>添加专业评论</Button> : null}
          </div>
          {commontList?.length
            ? (commontList?.map((item: any, index: number) => {
              return <CommonItemWrap showProductionName={ true }
                hiddenUser={ true } refresh={ mutate }
                key={ index } common={ item }
                CommontCount={ CommontCount }
                productionGroupDetail={ productionGroupDetail }
                commontUser={ userInfo }
              />;
            }))
            : <Empty />
          }
          <div className={ style.footerPage }>
            <Pagination total={ Number(pagination?.total) }
              showTotal={ (total: number) => `总计 ${ total } 条` }
              pageSize={ Number(pagination?.pageSize || PAGESIZE) }
              current={ Number(pagination?.current) }
              showQuickJumper={ true }
              onChange={ handleChangePage }
            />
          </div>
        </div>
      </Spin>
    </div>
  );
};

export default withAuthorized(UserCommontList, [Role.SYSTEM, Role.COMMENT, Role.EXPERT]);
