import { stringify } from 'qs';
import useSWR from '@/hooks/useSWR';
import { CommontItem } from '@/types/types';
import { useMemo } from 'react';
import { APP_ID } from '@/utils/constant.js';
import { useGetCommontCount } from '@/services/publicServices';

import { requestBasePath } from '@/services/login';

// 评论列表页-查询某个用户的评论 /queryUserCommentForPage
export function useGetUserCommontList(params: any) {
  const { query, pagination } = params || {};
  const { data, isValidating, mutate } = useSWR(
    params && (query?.subjectId || query?.commentUserId)
      ? [`${ requestBasePath }/comment/queryUserCommentForPage`, params]
      : null, {
      requestParams: {
        method: 'POST',
        body: {
          pagination,
          query: {
            ...query,
            appId: APP_ID
          }
        }
      }
    }
  );

  const commontList = useMemo(() => {
    return data?.data?.list || [];
  }, [data]);
  const commontIds = useMemo(() => {
    return commontList.map((item: CommontItem) => ({
      movieId: item?.subjectId,
      commentUserId: item?.commentUserId,
    }));
  }, [commontList]);
  const { CommontCount, isValidating: commontCountLoading } = useGetCommontCount(commontIds);

  return {
    commontList,
    pagination: data?.data?.pagination || {},
    isValidating: isValidating || commontCountLoading,
    CommontCount: CommontCount || {},
    mutate
  };
}